import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {ITransferServiceProvider} from "../../providers/i-transfer-service/i-transfer-service";
import {FundTransferOtherBankProvider} from "../../providers/fund-transfer-other-bank/fund-transfer-other-bank";
import {GoogleAnalyticsProvider} from "../../providers/google-analytics/google-analytics";


@IonicPage()
@Component({
  selector: 'page-utilitybilloperationselection',
  templateUrl: 'utilitybilloperationselection.html',
})
export class UtilitybilloperationselectionPage {

  utilityOperationSelectionError;
  accounts = [];
  transactional;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public tokenService: TokenServiceProvider,
              public storage: LocalStorageProvider,
              public maskTokenUtil: MaskTokenProvider,
              public cryptoUtil: CryptoParamUtilProvider,
              public transferService: ITransferServiceProvider,
              public ftOtherBankService: FundTransferOtherBankProvider,
              public gaProvider: GoogleAnalyticsProvider) {

    let dAccount = this.storage.retrieveFromStorage('accounts');
    if (dAccount == null || dAccount == "") {
      this.accounts = [];
    }
    else {

      dAccount = cryptoUtil.getDecryptVal(dAccount);
      dAccount = JSON.parse(dAccount);
      Object.keys(dAccount).forEach(key => {
        let value = dAccount[key];
        if (value.debitApproved == true) {
          this.accounts.push(value);
        }
      });
    }

    let dWelCome = this.storage.retrieveFromStorage('welcomeVal');
    if (dWelCome == null || dWelCome == "") {
    }
    else {
      dWelCome = cryptoUtil.getDecryptVal(dWelCome);
      dWelCome = JSON.parse(dWelCome);
      this.transactional = dWelCome.transactional;
    }
  }

  BillPayAccSelectionAction = function (account) {
    this.utilityOperationSelectionError = "";

    if (account.debitApproved == false) {
      this.utilityOperationSelectionError = "This Account is not permitted for Bill Payment.";
      return;
    }

    if (this.transactional == 1) {
      this.navCtrl.push('Utilitybillcompanyselection01Page', {
        'debitAccDetails': JSON.stringify(account)
      });
    }
    else {
      this.navCtrl.push('TxErrorPage');
    }
  };

  goToBillPaymentHistoryAction = function () {
    this.navCtrl.push('UtilityBillPaymentHistory01Page');
  };

  ionViewDidLoad() {
    this.gaProvider.startTrackerWithId("Utility Bill Payment");
  }

}



